/**
 Attorneys archive

 @since 0.1.0
 @package Inosencio
 */
(function ($) {
    'use strict';

    $(function () {

        var $attorney_select_items = $('.attorney-select').find('li'),
            $attorney_items = $('.attorneys').find('li');

        $attorney_items.filter(':not(:first-of-type)').hide();
        $attorney_select_items.first().addClass('active');

        $attorney_select_items.click(function () {

            $attorney_items.hide();
            $attorney_items.eq($(this).index()).show();

            $attorney_select_items.removeClass('active');
            $(this).addClass('active');
        });
    });
})(jQuery);
'use strict';

/**
 Site header functionality.

 @since 0.1.0
 @package Inosencio
 */
(function ($) {
    'use strict';

    $(function () {

        nav_position();

        $('.toggle-nav').click(function () {
            $('#mobile-nav').toggleClass('active');
        });
    });

    $(window).resize(nav_position);

    function nav_position() {

        $('#site-header').find('.site-nav').each(function () {

            $(this).css({
                top: $(this).parent().height() / 2 - $(this).height() / 2
            });
        });
    }
})(jQuery);
'use strict';

/**
 Main functions file.

 @since 0.1.0
 @package Inosencio
 */
(function ($) {
    'use strict';

    // Foundation

    $(document).foundation();

    $(function () {

        // Textillate
        if ($().textillate) {
            $('.textillate').textillate({
                loop: true,
                minDisplayTime: 1000,
                in: {
                    effect: 'flipInX',
                    delayScale: 0.75,
                    delay: 100
                },
                out: {
                    effect: 'flipOutX',
                    delayScale: 0.75,
                    delay: 50
                }
            });
        }
    });

    // Page loading overlay
    $('#site-content').append('<div id="page-load-overlay"><div class="loader fa fa-circle-o-notch fa-spin-fast" /></div>');

    setTimeout(function () {

        var $overlay = $('#page-load-overlay');

        if ($overlay.length) {
            $overlay.animate({
                opacity: 1
            }, 300);
        }
    }, 300);

    $(window).load(function () {
        $('#page-load-overlay').animate({
            opacity: 0
        }, {
            duration: 500,
            complete: function complete() {
                $(this).remove();
            }
        });
    });
})(jQuery);
'use strict';

/**
 Practice areas archive

 @since 0.1.0
 @package Inosencio
 */
(function ($) {
    'use strict';

    $(function () {

        // Prevent page jump
        prevent_jump();

        if (!$('body').hasClass('post-type-archive-practice_area')) {
            return;
        }

        var $practice_areas = $('.practice-areas'),
            hash = window.location.hash;

        $practice_areas.find('.practice-area-content').hide();

        $practice_areas.find('.practice-area > a').click(function (e) {

            console.log('click');

            var $practice_area = $(this).closest('.practice-area');

            $practice_areas.find('.practice-area').removeClass('active');
            $practice_areas.find('.practice-area-content').hide();
            $practice_area.siblings('.practice-area-content').show();
            $practice_area.addClass('active');

            // Scroll
            var href = $(this).attr('href'),
                hash = href.substring(href.indexOf("#") + 1),
                target = $('#' + hash);

            if (target.length) {
                $('html,body').animate({
                    scrollTop: target.offset().top - $('.top-bar').height() - 10
                }, 500);
            }

            e.preventDefault();
            return false;
        });

        // Scroll to practice area if hash is in page load
        if (hash.length) {
            setTimeout(function () {
                $(hash).find('> a').click();
            }, 100);
        }
    });

    function prevent_jump() {

        if (window.location.hash) {
            setTimeout(function () {
                window.scrollTo(0, 0);
            }, 1);
        }
    }
})(jQuery);
'use strict';

/**
 Search bar functionality.

 @since 0.1.0
 @package Inosencio
 */
(function ($) {
    'use strict';

    $(function () {

        var $top_bar = $('#site-header').find('.top-bar'),
            $search_container = $top_bar.find('.search'),
            $search_input = $search_container.find('input[type="search"]'),
            $search_button = $search_container.find('.search-button'),
            placeholder = $search_input.data('placeholder');

        $search_input.val(placeholder).on('focus', function () {

            if ($(this).val() == placeholder) {
                $(this).val('');
            }
        }).blur(function () {

            if (!$(this).val()) {
                $(this).val(placeholder);
            }
        }).each(resizeInput);

        function resizeInput() {
            $(this).attr('size', $(this).val().length);
        }

        $search_button.click(function (e) {

            $search_input.focus();

            e.preventDefault();
            return false;
        });
    });
})(jQuery);
